import { useState, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useQuery } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { RefreshCw, ShoppingBag, CalendarCheck, TrendingUp, Package, Calendar as CalendarIcon, Eye, User, MapPin, CreditCard, Globe, Mail, Phone, Store, CheckCircle, Clock, DollarSign, Info } from "lucide-react";
import { supabase } from "@/integrations/supabase/client";
import { format, subDays, startOfDay, endOfDay, isWithinInterval } from "date-fns";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, LineChart, Line, PieChart, Pie, Cell } from "recharts";
import { cn } from "@/lib/utils";

interface StatsInfo {
  reserve_count: number;
  order_count: number;
}

interface OrderData {
  id?: string;
  created_at?: string;
  status?: string;
  total?: number;
  restaurant_id?: string;
  [key: string]: any;
}

interface ReserveData {
  id?: string;
  created_at?: string;
  status?: string;
  date?: string;
  time?: string;
  guests?: number;
  restaurant_id?: string;
  [key: string]: any;
}

type DateRange = "today" | "7days" | "30days" | "90days" | "180days" | "all" | "custom";

const CHART_COLORS = ['hsl(var(--primary))', 'hsl(var(--chart-2))', 'hsl(var(--chart-3))', 'hsl(var(--chart-4))', 'hsl(var(--chart-5))'];

const AdminOrderStats = () => {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState<"overview" | "orders" | "reserves">("overview");
  const [dateRange, setDateRange] = useState<DateRange>("all");
  const [customStartDate, setCustomStartDate] = useState<Date | undefined>(undefined);
  const [customEndDate, setCustomEndDate] = useState<Date | undefined>(undefined);
  const [selectedOrder, setSelectedOrder] = useState<OrderData | null>(null);
  const [selectedReserve, setSelectedReserve] = useState<ReserveData | null>(null);

  // Helper to render detail item
  const renderDetailItem = (label: string, value: any) => {
    if (value === null || value === undefined || value === '') return null;
    const displayValue = typeof value === 'object' ? JSON.stringify(value, null, 2) : String(value);
    return (
      <div className="flex flex-col gap-1 py-2 border-b border-border/50 last:border-0">
        <span className="text-xs text-muted-foreground font-medium">{label}</span>
        <span className="text-sm break-all whitespace-pre-wrap">{displayValue}</span>
      </div>
    );
  };

  // Calculate date range
  const getDateRange = () => {
    const now = new Date();
    switch (dateRange) {
      case "today":
        return { start: startOfDay(now), end: endOfDay(now) };
      case "7days":
        return { start: startOfDay(subDays(now, 6)), end: endOfDay(now) };
      case "30days":
        return { start: startOfDay(subDays(now, 29)), end: endOfDay(now) };
      case "90days":
        return { start: startOfDay(subDays(now, 89)), end: endOfDay(now) };
      case "180days":
        return { start: startOfDay(subDays(now, 179)), end: endOfDay(now) };
      case "all":
        return { start: new Date(2020, 0, 1), end: endOfDay(now) };
      case "custom":
        return {
          start: customStartDate ? startOfDay(customStartDate) : startOfDay(subDays(now, 29)),
          end: customEndDate ? endOfDay(customEndDate) : endOfDay(now),
        };
      default:
        return { start: new Date(2020, 0, 1), end: endOfDay(now) };
    }
  };

  // Fetch total counts - auto refresh every 3 seconds
  const { data: statsInfo, isLoading: isLoadingInfo, refetch: refetchInfo } = useQuery({
    queryKey: ['external-stats-info'],
    queryFn: async (): Promise<StatsInfo> => {
      const { data, error } = await supabase.functions.invoke('external-stats-api', {
        body: { action: 'get_info' }
      });
      if (error) throw error;
      return data;
    },
    refetchInterval: 3000,
  });

  // Fetch order data - auto refresh every 3 seconds (only orders with name starting with "T")
  const { data: ordersData, isLoading: isLoadingOrders, refetch: refetchOrders } = useQuery({
    queryKey: ['external-orders-data'],
    queryFn: async (): Promise<OrderData[]> => {
      const { data, error } = await supabase.functions.invoke('external-stats-api', {
        body: { 
          action: 'query_data', 
          table: 'order', 
          query: { name: { $regex: '^T' } },
          count: 10000 
        }
      });
      if (error) throw error;
      return Array.isArray(data) ? data : [];
    },
    refetchInterval: 3000,
  });

  // Fetch reserve data - auto refresh every 3 seconds
  const { data: reservesData, isLoading: isLoadingReserves, refetch: refetchReserves } = useQuery({
    queryKey: ['external-reserves-data'],
    queryFn: async (): Promise<ReserveData[]> => {
      const { data, error } = await supabase.functions.invoke('external-stats-api', {
        body: { action: 'query_data', table: 'reserve', count: 10000 }
      });
      if (error) throw error;
      return Array.isArray(data) ? data : [];
    },
    refetchInterval: 3000,
  });

  // Filter data by date range and sort by time (newest first)
  const { filteredOrders, filteredReserves } = useMemo(() => {
    const { start, end } = getDateRange();
    
    const filteredOrders = (ordersData?.filter(order => {
      if (!order.created_at) return false;
      const orderDate = new Date(order.created_at);
      return isWithinInterval(orderDate, { start, end });
    }) || []).sort((a, b) => {
      const dateA = a.created_at ? new Date(a.created_at).getTime() : 0;
      const dateB = b.created_at ? new Date(b.created_at).getTime() : 0;
      return dateB - dateA; // Newest first
    });

    const filteredReserves = (reservesData?.filter(reserve => {
      // 优先使用 creationDate（记录创建时间），而非 date（预约日期）
      const dateStr = reserve.creationDate || reserve.created_at || reserve.date;
      if (!dateStr) return false;
      const reserveDate = new Date(dateStr);
      return isWithinInterval(reserveDate, { start, end });
    }) || []).sort((a, b) => {
      // Sort by creation date (newest first)
      const dateTimeA = a.creationDate ? new Date(a.creationDate).getTime() : 0;
      const dateTimeB = b.creationDate ? new Date(b.creationDate).getTime() : 0;
      return dateTimeB - dateTimeA; // Latest first
    });

    return { filteredOrders, filteredReserves };
  }, [ordersData, reservesData, dateRange, customStartDate, customEndDate]);

  const handleRefresh = () => {
    refetchInfo();
    refetchOrders();
    refetchReserves();
  };

  const isLoading = isLoadingInfo || isLoadingOrders || isLoadingReserves;

  // Process data for charts - use filtered data
  const processChartData = () => {
    const ordersByDate: Record<string, number> = {};
    const reservesByDate: Record<string, number> = {};

    filteredOrders.forEach(order => {
      const date = order.created_at ? format(new Date(order.created_at), 'MM-dd') : 'Unknown';
      ordersByDate[date] = (ordersByDate[date] || 0) + 1;
    });

    filteredReserves.forEach(reserve => {
      const date = reserve.created_at || reserve.date;
      const formattedDate = date ? format(new Date(date), 'MM-dd') : 'Unknown';
      reservesByDate[formattedDate] = (reservesByDate[formattedDate] || 0) + 1;
    });

    // Combine dates and create chart data
    const allDates = [...new Set([...Object.keys(ordersByDate), ...Object.keys(reservesByDate)])].sort();
    const maxDays = dateRange === "today" ? 1 : dateRange === "7days" ? 7 : dateRange === "30days" ? 14 : 30;
    return allDates.slice(-maxDays).map(date => ({
      date,
      orders: ordersByDate[date] || 0,
      reserves: reservesByDate[date] || 0,
    }));
  };

  const chartData = processChartData();

  const pieData = [
    { name: t('admin.orderStats.orders', '外卖订单'), value: filteredOrders.length },
    { name: t('admin.orderStats.reserves', '订台预约'), value: filteredReserves.length },
  ];

  return (
    <div className="container mx-auto py-6 space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold">{t('admin.orderStats.title', '外卖订台统计')}</h1>
          <p className="text-muted-foreground">{t('admin.orderStats.description', '查看外卖订单和订台预约数据')}</p>
        </div>
        <div className="flex flex-wrap items-center gap-2">
          {/* Date Range Selector */}
          <Select value={dateRange} onValueChange={(v) => setDateRange(v as DateRange)}>
            <SelectTrigger className="w-[140px]">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">{t('admin.orderStats.allTime', '全部')}</SelectItem>
              <SelectItem value="today">{t('admin.orderStats.today', '今天')}</SelectItem>
              <SelectItem value="7days">{t('admin.orderStats.last7Days', '最近7天')}</SelectItem>
              <SelectItem value="30days">{t('admin.orderStats.last30Days', '最近30天')}</SelectItem>
              <SelectItem value="90days">{t('admin.orderStats.last90Days', '最近90天')}</SelectItem>
              <SelectItem value="180days">{t('admin.orderStats.last180Days', '最近半年')}</SelectItem>
              <SelectItem value="custom">{t('admin.orderStats.customRange', '自定义')}</SelectItem>
            </SelectContent>
          </Select>

          {/* Custom Date Pickers */}
          {dateRange === "custom" && (
            <div className="flex items-center gap-2">
              <Popover>
                <PopoverTrigger asChild>
                  <Button variant="outline" className={cn("w-[130px] justify-start text-left font-normal", !customStartDate && "text-muted-foreground")}>
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {customStartDate ? format(customStartDate, "yyyy-MM-dd") : t('admin.orderStats.startDate', '开始日期')}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar mode="single" selected={customStartDate} onSelect={setCustomStartDate} initialFocus />
                </PopoverContent>
              </Popover>
              <span className="text-muted-foreground">-</span>
              <Popover>
                <PopoverTrigger asChild>
                  <Button variant="outline" className={cn("w-[130px] justify-start text-left font-normal", !customEndDate && "text-muted-foreground")}>
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {customEndDate ? format(customEndDate, "yyyy-MM-dd") : t('admin.orderStats.endDate', '结束日期')}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar mode="single" selected={customEndDate} onSelect={setCustomEndDate} initialFocus />
                </PopoverContent>
              </Popover>
            </div>
          )}

          <Button onClick={handleRefresh} variant="outline" disabled={isLoading}>
            <RefreshCw className={`h-4 w-4 mr-2 ${isLoading ? 'animate-spin' : ''}`} />
            {t('common.refresh', '刷新')}
          </Button>
        </div>
      </div>

      {/* Total Counts - Fixed from API */}
      <div className="grid grid-cols-2 gap-4">
        <Card className="border-primary/30 bg-primary/5">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.totalOrders', '外卖总订单')}</CardTitle>
            <ShoppingBag className="h-5 w-5 text-primary" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-10 w-24" />
            ) : (
              <div className="text-3xl font-bold text-primary">{statsInfo?.order_count ?? 0}</div>
            )}
          </CardContent>
        </Card>
        <Card className="border-chart-2/30 bg-chart-2/5">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.totalReserves', '订台总预约')}</CardTitle>
            <CalendarCheck className="h-5 w-5 text-chart-2" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-10 w-24" />
            ) : (
              <div className="text-3xl font-bold text-chart-2">{statsInfo?.reserve_count ?? 0}</div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Period Summary Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.periodOrders', '期间外卖订单')}</CardTitle>
            <ShoppingBag className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-8 w-20" />
            ) : (
              <div className="text-2xl font-bold">{filteredOrders.length}</div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.periodReserves', '期间订台预约')}</CardTitle>
            <CalendarCheck className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-8 w-20" />
            ) : (
              <div className="text-2xl font-bold">{filteredReserves.length}</div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.periodTotal', '期间总交易')}</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-8 w-20" />
            ) : (
              <div className="text-2xl font-bold">{filteredOrders.length + filteredReserves.length}</div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">{t('admin.orderStats.ratio', '订单占比')}</CardTitle>
            <Package className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-8 w-20" />
            ) : (
              <>
                <div className="text-2xl font-bold">
                  {(filteredOrders.length + filteredReserves.length) > 0
                    ? `${Math.round((filteredOrders.length / (filteredOrders.length + filteredReserves.length)) * 100)}%`
                    : '0%'}
                </div>
                <p className="text-xs text-muted-foreground">{t('admin.orderStats.orderVsReserve', '外卖 vs 订台')}</p>
              </>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Charts Section */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Trend Chart */}
        <Card className="lg:col-span-2">
          <CardHeader>
            <CardTitle>{t('admin.orderStats.trendChart', '趋势图')}</CardTitle>
            <CardDescription>{t('admin.orderStats.selectedPeriod', '所选时间段数据')}</CardDescription>
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-[300px] w-full" />
            ) : (
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={chartData}>
                  <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                  <XAxis dataKey="date" className="text-xs" />
                  <YAxis className="text-xs" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: 'hsl(var(--background))',
                      border: '1px solid hsl(var(--border))',
                      borderRadius: '8px',
                    }}
                  />
                  <Line
                    type="monotone"
                    dataKey="orders"
                    stroke="hsl(var(--primary))"
                    strokeWidth={2}
                    name={t('admin.orderStats.orders', '外卖订单')}
                  />
                  <Line
                    type="monotone"
                    dataKey="reserves"
                    stroke="hsl(var(--chart-2))"
                    strokeWidth={2}
                    name={t('admin.orderStats.reserves', '订台预约')}
                  />
                </LineChart>
              </ResponsiveContainer>
            )}
          </CardContent>
        </Card>

        {/* Pie Chart */}
        <Card>
          <CardHeader>
            <CardTitle>{t('admin.orderStats.distribution', '分布')}</CardTitle>
            <CardDescription>{t('admin.orderStats.orderTypeRatio', '订单类型占比')}</CardDescription>
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <Skeleton className="h-[300px] w-full" />
            ) : (
              <ResponsiveContainer width="100%" height={300}>
                <PieChart>
                  <Pie
                    data={pieData}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={100}
                    paddingAngle={5}
                    dataKey="value"
                    label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                  >
                    {pieData.map((_, index) => (
                      <Cell key={`cell-${index}`} fill={CHART_COLORS[index % CHART_COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip
                    contentStyle={{
                      backgroundColor: 'hsl(var(--background))',
                      border: '1px solid hsl(var(--border))',
                      borderRadius: '8px',
                    }}
                  />
                </PieChart>
              </ResponsiveContainer>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Data Tables */}
      <Tabs value={activeTab} onValueChange={(v) => setActiveTab(v as any)}>
        <TabsList>
          <TabsTrigger value="overview">{t('admin.orderStats.overview', '概览')}</TabsTrigger>
          <TabsTrigger value="orders">{t('admin.orderStats.ordersList', '外卖订单')}</TabsTrigger>
          <TabsTrigger value="reserves">{t('admin.orderStats.reservesList', '订台预约')}</TabsTrigger>
        </TabsList>

        <TabsContent value="overview">
          <Card>
            <CardHeader>
              <CardTitle>{t('admin.orderStats.dailyStats', '每日统计')}</CardTitle>
            </CardHeader>
            <CardContent>
              {isLoading ? (
                <Skeleton className="h-[300px] w-full" />
              ) : (
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={chartData}>
                    <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                    <XAxis dataKey="date" className="text-xs" />
                    <YAxis className="text-xs" />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'hsl(var(--background))',
                        border: '1px solid hsl(var(--border))',
                        borderRadius: '8px',
                      }}
                    />
                    <Bar dataKey="orders" fill="hsl(var(--primary))" name={t('admin.orderStats.orders', '外卖订单')} />
                    <Bar dataKey="reserves" fill="hsl(var(--chart-2))" name={t('admin.orderStats.reserves', '订台预约')} />
                  </BarChart>
                </ResponsiveContainer>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="orders">
          <Card>
            <CardHeader>
              <CardTitle>{t('admin.orderStats.recentOrders', '最近外卖订单')}</CardTitle>
              <CardDescription>{t('admin.orderStats.showingRecent', '显示最近的订单记录')}</CardDescription>
            </CardHeader>
            <CardContent>
              {isLoadingOrders ? (
                <Skeleton className="h-[400px] w-full" />
              ) : (
                <ScrollArea className="h-[400px]">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>{t('admin.orderStats.orderNumber', '订单号')}</TableHead>
                        <TableHead>{t('admin.orderStats.customer', '客户')}</TableHead>
                        <TableHead>{t('admin.orderStats.restaurant', '餐厅')}</TableHead>
                        <TableHead className="text-right">{t('admin.orderStats.amount', '金额')}</TableHead>
                        <TableHead>{t('admin.orderStats.pickupTime', '取餐时间')}</TableHead>
                        <TableHead>{t('admin.orderStats.paymentStatus', '支付状态')}</TableHead>
                        <TableHead></TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredOrders.slice(0, 50).map((order, index) => (
                        <TableRow 
                          key={order.id || index}
                          className="cursor-pointer hover:bg-muted/50"
                          onClick={() => setSelectedOrder(order)}
                        >
                          <TableCell className="font-mono text-xs">
                            {order.name || `#${index + 1}`}
                          </TableCell>
                          <TableCell>
                            <div className="flex flex-col">
                              <span className="text-sm font-medium">
                                {order.customer?.name || '-'}
                              </span>
                              <span className="text-xs text-muted-foreground truncate max-w-[150px]">
                                {order.customer?.email || ''}
                              </span>
                            </div>
                          </TableCell>
                          <TableCell>
                            <span className="text-xs">{order.restaurant || '-'}</span>
                          </TableCell>
                          <TableCell className="text-right font-medium">
                            €{order.total_price || '0'}
                          </TableCell>
                          <TableCell>
                            <div className="flex flex-col">
                              <span className="text-xs">{order.pickupDate || order.pickup_date || '-'}</span>
                              <span className="text-xs text-muted-foreground">{order.pickupTime || ''}</span>
                            </div>
                          </TableCell>
                          <TableCell>
                            <Badge 
                              variant={order.pay_state === 'paid' ? 'default' : 'secondary'} 
                              className={cn("text-xs", order.pay_state === 'paid' && "bg-green-500")}
                            >
                              {order.pay_state === 'paid' ? '已付款' : (order.pay_state || 'pending')}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            <Button variant="ghost" size="sm" className="h-7">
                              <Eye className="h-4 w-4" />
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                      {filteredOrders.length === 0 && (
                        <TableRow>
                          <TableCell colSpan={7} className="text-center text-muted-foreground">
                            {t('common.noData', '暂无数据')}
                          </TableCell>
                        </TableRow>
                      )}
                    </TableBody>
                  </Table>
                </ScrollArea>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="reserves">
          <Card>
            <CardHeader>
              <CardTitle>{t('admin.orderStats.recentReserves', '最近订台预约')}</CardTitle>
              <CardDescription>{t('admin.orderStats.showingRecent', '显示最近的预约记录')}</CardDescription>
            </CardHeader>
            <CardContent>
              {isLoadingReserves ? (
                <Skeleton className="h-[400px] w-full" />
              ) : (
                <ScrollArea className="h-[400px]">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>{t('admin.orderStats.reserveNo', '订台号')}</TableHead>
                        <TableHead>{t('admin.orderStats.customerName', '预订人')}</TableHead>
                        <TableHead>{t('admin.orderStats.restaurant', '餐厅')}</TableHead>
                        <TableHead>{t('admin.orderStats.creationDate', '创建时间')}</TableHead>
                        <TableHead>{t('admin.orderStats.reserveDateTime', '预约日期/时间')}</TableHead>
                        <TableHead className="text-center">{t('admin.orderStats.guests', '人数')}</TableHead>
                        <TableHead>{t('common.status', '状态')}</TableHead>
                        <TableHead></TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredReserves.slice(0, 50).map((reserve, index) => (
                        <TableRow 
                          key={reserve.id || index}
                          className="cursor-pointer hover:bg-muted/50"
                          onClick={() => setSelectedReserve(reserve)}
                        >
                          <TableCell>
                            <Badge variant="outline" className="font-mono">{reserve.name || '-'}</Badge>
                          </TableCell>
                          <TableCell className="font-medium">
                            {reserve.customerName || '-'}
                          </TableCell>
                          <TableCell className="max-w-[150px] truncate">
                            {reserve.restaurant || '-'}
                          </TableCell>
                          <TableCell>
                            <span className="text-xs text-muted-foreground">
                              {reserve.creationDate ? format(new Date(reserve.creationDate), 'yyyy-MM-dd HH:mm') : '-'}
                            </span>
                          </TableCell>
                          <TableCell>
                            <div className="flex flex-col">
                              <span className="text-sm">{reserve.date || '-'}</span>
                              <span className="text-xs text-muted-foreground">{reserve.time || '-'}</span>
                            </div>
                          </TableCell>
                          <TableCell className="text-center">
                            <Badge variant="outline">{reserve.numberPeople || reserve.guests || '-'}</Badge>
                          </TableCell>
                          <TableCell>
                            <Badge variant={reserve.status === 'confirmed' ? 'default' : 'secondary'}>
                              {reserve.status || 'pending'}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            <Button variant="ghost" size="sm" className="h-7">
                              <Eye className="h-4 w-4" />
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                      {filteredReserves.length === 0 && (
                        <TableRow>
                          <TableCell colSpan={8} className="text-center text-muted-foreground">
                            {t('common.noData', '暂无数据')}
                          </TableCell>
                        </TableRow>
                      )}
                    </TableBody>
                  </Table>
                </ScrollArea>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Order Detail Dialog */}
      <Dialog open={!!selectedOrder} onOpenChange={(open) => !open && setSelectedOrder(null)}>
        <DialogContent className="max-w-2xl max-h-[85vh] overflow-hidden">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <ShoppingBag className="h-5 w-5 text-primary" />
              {t('admin.orderStats.orderDetail', '外卖订单详情')}
            </DialogTitle>
            <DialogDescription className="flex items-center gap-2">
              {selectedOrder?.name && (
                <Badge variant="outline" className="font-mono">
                  {selectedOrder.name}
                </Badge>
              )}
              {selectedOrder?.pay_state && (
                <Badge variant={selectedOrder.pay_state === 'paid' ? 'default' : 'secondary'} className={selectedOrder.pay_state === 'paid' ? 'bg-green-500' : ''}>
                  {selectedOrder.pay_state === 'paid' ? '已付款' : selectedOrder.pay_state}
                </Badge>
              )}
            </DialogDescription>
          </DialogHeader>
          
          {selectedOrder && (
            <ScrollArea className="max-h-[65vh] pr-4">
              <div className="space-y-4">
                {/* Order Summary */}
                <div className="rounded-lg border bg-primary/5 p-4">
                  <div className="flex justify-between items-start">
                    <div>
                      <p className="text-sm text-muted-foreground">订单总额</p>
                      <p className="text-2xl font-bold text-primary">
                        € {selectedOrder.total_price || '0'}
                      </p>
                    </div>
                    <div className="text-right">
                      <p className="text-sm text-muted-foreground">创建时间</p>
                      <p className="text-sm font-medium">
                        {selectedOrder.created_at 
                          ? format(new Date(selectedOrder.created_at), 'yyyy-MM-dd HH:mm:ss')
                          : '-'}
                      </p>
                    </div>
                  </div>
                </div>

                {/* Customer Info */}
                {selectedOrder.customer && (
                  <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                    <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                      <User className="h-4 w-4" />
                      客户信息
                    </h4>
                    <div className="grid gap-3">
                      {selectedOrder.customer.name && (
                        <div className="flex items-center gap-2">
                          <User className="h-4 w-4 text-muted-foreground" />
                          <span className="text-sm font-medium">{selectedOrder.customer.name}</span>
                        </div>
                      )}
                      {selectedOrder.customer.phone && (
                        <div className="flex items-center gap-2">
                          <Phone className="h-4 w-4 text-muted-foreground" />
                          <span className="text-sm">
                            {selectedOrder.customer.phone.includes('#') 
                              ? `+${selectedOrder.customer.phone.replace('#', ' ')}` 
                              : selectedOrder.customer.phone}
                          </span>
                        </div>
                      )}
                      {selectedOrder.customer.email && (
                        <div className="flex items-center gap-2">
                          <Mail className="h-4 w-4 text-muted-foreground" />
                          <span className="text-sm">{selectedOrder.customer.email}</span>
                        </div>
                      )}
                    </div>
                  </div>
                )}

                {/* Pickup Info */}
                <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                  <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                    <Clock className="h-4 w-4" />
                    取餐信息
                  </h4>
                  <div className="grid gap-2">
                    {selectedOrder.restaurant && (
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-muted-foreground">餐厅</span>
                        <span className="font-medium">{selectedOrder.restaurant}</span>
                      </div>
                    )}
                    {(selectedOrder.pickupDate || selectedOrder.pickup_date) && (
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-muted-foreground">取餐日期</span>
                        <span className="font-medium">{selectedOrder.pickupDate || selectedOrder.pickup_date}</span>
                      </div>
                    )}
                    {selectedOrder.pickupTime && (
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-muted-foreground">取餐时间</span>
                        <Badge variant="outline" className="font-medium">{selectedOrder.pickupTime}</Badge>
                      </div>
                    )}
                  </div>
                </div>

                {/* Order Items (line_items) */}
                {selectedOrder.line_items && Array.isArray(selectedOrder.line_items) && selectedOrder.line_items.length > 0 && (
                  <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                    <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                      <Package className="h-4 w-4" />
                      订单商品 ({selectedOrder.line_items.length})
                    </h4>
                    <div className="space-y-2">
                      {selectedOrder.line_items.map((item: any, idx: number) => (
                        <div key={idx} className="flex justify-between items-start py-2 border-b border-border/50 last:border-0">
                          <div className="flex-1 space-y-1">
                            <p className="text-sm font-medium">{item.name || item.title || '商品'}</p>
                            {item.sku && (
                              <p className="text-xs text-muted-foreground font-mono">SKU: {item.sku}</p>
                            )}
                            <p className="text-xs text-muted-foreground">
                              数量: {item.quantity || 1} × €{item.price || '0'}
                            </p>
                            {item.properties && Array.isArray(item.properties) && item.properties.length > 0 && (
                              <div className="text-xs text-muted-foreground">
                                {item.properties.map((prop: any, pIdx: number) => (
                                  <span key={pIdx} className="mr-2">
                                    {prop.name}: {prop.value}
                                  </span>
                                ))}
                              </div>
                            )}
                          </div>
                          <p className="font-medium text-primary">
                            €{((item.quantity || 1) * parseFloat(item.price || '0')).toFixed(2)}
                          </p>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Payment Info */}
                <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                  <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                    <CreditCard className="h-4 w-4" />
                    付款信息
                  </h4>
                  <div className="grid gap-2">
                    {selectedOrder.pay_type && (
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-muted-foreground">付款方式</span>
                        <Badge variant="outline">{selectedOrder.pay_type}</Badge>
                      </div>
                    )}
                    {selectedOrder.pay_state && (
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-muted-foreground">付款状态</span>
                        <Badge variant={selectedOrder.pay_state === 'paid' ? 'default' : 'secondary'} className={selectedOrder.pay_state === 'paid' ? 'bg-green-500' : ''}>
                          {selectedOrder.pay_state === 'paid' ? '已付款' : selectedOrder.pay_state}
                        </Badge>
                      </div>
                    )}
                    <div className="flex justify-between items-center font-bold border-t pt-2 mt-2">
                      <span>总计</span>
                      <span className="text-primary">€{selectedOrder.total_price || '0'}</span>
                    </div>
                  </div>
                </div>

                {/* Note */}
                {selectedOrder.note && (
                  <div className="rounded-lg border p-4 space-y-2">
                    <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                      <Info className="h-4 w-4" />
                      备注
                    </h4>
                    <p className="text-sm">{selectedOrder.note}</p>
                  </div>
                )}

                {/* ID Reference */}
                <div className="pt-2 border-t">
                  <p className="text-xs text-muted-foreground font-mono">
                    ID: {selectedOrder.id || '-'} {selectedOrder._id && `| MongoDB: ${selectedOrder._id}`}
                  </p>
                </div>
              </div>
            </ScrollArea>
          )}
        </DialogContent>
      </Dialog>

      {/* Reserve Detail Dialog */}
      <Dialog open={!!selectedReserve} onOpenChange={(open) => !open && setSelectedReserve(null)}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <CalendarCheck className="h-5 w-5 text-chart-2" />
              {t('admin.orderStats.reserveDetail', '订台预约详情')}
              {selectedReserve?.name && (
                <Badge variant="outline" className="font-mono ml-2">{selectedReserve.name}</Badge>
              )}
            </DialogTitle>
          </DialogHeader>
          
          {selectedReserve && (
            <div className="space-y-4">
              {/* Customer Info Section */}
              <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                  <User className="h-4 w-4" />
                  {t('admin.orderStats.customerInfo', '客户信息')}
                </h4>
                <div className="grid gap-2">
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('admin.orderStats.customerName', '预订人')}</span>
                    <span className="font-medium">{selectedReserve.customerName || '-'}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('common.phone', '电话')}</span>
                    <span className="font-medium">{selectedReserve.phone || '-'}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('common.email', '邮箱')}</span>
                    <span className="font-medium text-sm">{selectedReserve.email || '-'}</span>
                  </div>
                </div>
              </div>

              {/* Reservation Details Section */}
              <div className="rounded-lg border bg-muted/30 p-4 space-y-3">
                <h4 className="text-sm font-medium text-muted-foreground flex items-center gap-2">
                  <CalendarCheck className="h-4 w-4" />
                  {t('admin.orderStats.reservationDetails', '预约信息')}
                </h4>
                <div className="grid gap-2">
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('admin.orderStats.restaurant', '餐厅')}</span>
                    <span className="font-medium">{selectedReserve.restaurant || '-'}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('admin.orderStats.reserveDate', '预约日期')}</span>
                    <span className="font-medium">{selectedReserve.date || '-'}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('admin.orderStats.reserveTime', '预约时间')}</span>
                    <span className="font-medium">{selectedReserve.time || '-'}</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-muted-foreground">{t('admin.orderStats.guests', '人数')}</span>
                    <Badge variant="outline" className="font-medium">
                      {selectedReserve.numberPeople || selectedReserve.guests || '-'} {t('admin.orderStats.people', '人')}
                    </Badge>
                  </div>
                </div>
              </div>

              {/* Status Section */}
              <div className="flex justify-between items-center px-1">
                <span className="text-sm text-muted-foreground">{t('common.status', '状态')}</span>
                <Badge variant={selectedReserve.status === 'confirmed' ? 'default' : 'secondary'}>
                  {selectedReserve.status || 'pending'}
                </Badge>
              </div>

              {/* ID Reference (collapsed) */}
              <div className="pt-2 border-t">
                <p className="text-xs text-muted-foreground font-mono">
                  ID: {selectedReserve.id || selectedReserve._id || '-'}
                </p>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default AdminOrderStats;
